import React,{Component} from 'react'
import { hashHistory } from 'react-router';
import { Layout, Breadcrumb, Button, Table, Popconfirm, message, Spin } from 'antd';
const { Content } = Layout;

class List extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: true,
            columns: [{
                title: "信息类别",
                render: category => category == "1" ? "资金" : "项目"
            }, {
                title: "信息标题",
                dataIndex: "title"
            }, {
                title: "发布人",
                render: data =>
                    <div className="divClick" onClick={this.goToUserDetail.bind(this, data.publisher.id)}>{data.publisher.name}</div>
            }, {
                title: "被举报次数",
                render: data =>
                    <div className="divClick" onClick={this.showReportList.bind(this, data.id)}>{data.number}</div>
            }, {
                title: "操作",
                render: data =>
                    <div style={{textAlign: "center"}}>
                        <Popconfirm placement="left" title="是否确认此操作?" onConfirm={this.setForbidden.bind(this, data)} okText="确认" cancelText="取消">
                            <Button type="danger">{data.forbidden ? "解禁" : "禁言"}</Button>
                        </Popconfirm>
                        <Popconfirm placement="left" title="是否确认删除?" onConfirm={this.deleteReport.bind(this, data.id)} okText="确认" cancelText="取消">
                            <Button style={{marginLeft:20}} type="danger">删除</Button>
                        </Popconfirm>
                    </div>

            }],
            dataSource: [],
            pagination: {
                current: 1,
                total: 0
            }
        };
        ['loadListData', 'changePage', 'deleteReport'].forEach(m=>this[m] = this[m].bind(this))
    }

    componentWillMount() {
        this.loadListData(1);
    }

    loadListData(page) {
        let that = this;
        ajax({
            url: "/report/getInfoList",
            method: "post",
            data:  {
                current: page
            },
            callback: function(data) {
                if(data.status == 200) {
                    that.setState({
                        pagination: {
                            current: data.data.current,
                            total: data.data.total
                        },
                        dataSource: data.data.list,
                        loading: false
                    })
                } else {
                    message.error(data.errMsg, 1);
                }
            }
        })
    }

    deleteReport(id) {
        let that = this;
        ajax({
            url: "report/deleteInfo",
            data: {
                id: id
            },
            callback: function(data) {
                if(data.status == 200) {
                    message.success("删除成功!", 1);
                    let newDataSource = that.state.dataSource.filter(item => { return item.id != id});
                    that.setState({dataSource: newDataSource});
                } else {
                    message.error(data.errMsg, 1);
                }
            }
        })
    }

    setForbidden(selectedData) {
        let that = this;
        ajax({
            url: "user/manage/setForbidden",
            data: {
                id: selectedData.id,
                forbidden: !selectedData.forbidden
            },
            callback: function(data) {
                if(data.status == 200) {
                    message.success("设置成功!", 1);
                    that.state.dataSource.filter(item => {
                        if(item.id == selectedData.id) {
                            item.forbidden = !item.forbidden
                        }
                    });
                    that.setState({dataSource: that.state.dataSource})
                } else {
                    message.error(data.errMsg, 1);
                }
            }
        })

    }

    goToUserDetail(id) {
        hashHistory.push("/user/manage/detail/" + id);
    }

    showReportList(id) {
        hashHistory.push("/report/detail/" + id);
    }

    changePage(page) {
        this.loadListData(page.current);
    }

    render() {
        return (
            <Layout style={{ padding: '0 24px 24px' }}>
                <Breadcrumb style={{ margin: '12px 0' }}>
                    {
                        this.props.routes.map(function(item, index) {
                            return <Breadcrumb.Item key="">{item.breadcrumbName}</Breadcrumb.Item>
                        })
                    }
                </Breadcrumb>
                <Content style={{ background: '#fff', padding: 24, margin: 0, minHeight: 280 }}>
                    <Spin spinning={this.state.loading} tip="Loading...">
                        <Table
                            bordered
                            dataSource={this.state.dataSource}
                            columns={this.state.columns}
                            pagination={this.state.pagination}
                            onChange={this.changePage}
                        />
                    </Spin>
                </Content>
            </Layout>
        )
    }
}

module.exports = List;


