import React,{Component} from 'react'
import { hashHistory } from 'react-router';
import { Layout, Breadcrumb, Button, Table, Popconfirm, message } from 'antd';
const { Content } = Layout;

export class List extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            columns: [{
                title: '标题',
                dataIndex: 'title',
                className: "column-width-25"
            }, {
                title: '来源',
                dataIndex: "source",
                className: "column-width-25"

            }, {
                title: '添加时间',
                className: "column-width-25",
                dataIndex: 'createTime'
            }, {
                title: "操作",
                className: "column-width-25",
                render: data => <div style={{"textAlign": "center"}}>
                    <Button type="default" onClick={this.goToDetail.bind(this, data.key)} style={{"marginRight": "20px"}}>查看
                    </Button>
                    <Button type="primary" onClick={this.goToEdit.bind(this, data.key)} style={{"marginRight": "20px"}}>修改
                    </Button>
                    <Popconfirm placement="left" title="是否确认删除?" onConfirm={this.deleteInfo.bind(this, data.key)}
                                okText="确认" cancelText="取消">
                        <Button type="danger">删除</Button>
                    </Popconfirm>
                </div>
            }],
            dataSource: [],
            pagination: {
                current: 1,
                total: 0
            },
            loading: false
        };
        ['changePage', 'deleteInfo'].forEach(m=>this[m] = this[m].bind(this))
    }

    componentWillMount() {
        this.getInfoList(1);
    }

    getInfoList(currentPage) {
        let that = this;
        this.setState({loading: true});
        ajax({
            url: "/platform/report/getInfoList",
            data: {
                    current: currentPage ? currentPage : 1
            },
            callback: function(data) {
                that.setState(
                    {
                        loading: false,
                        pagination: {
                            current: currentPage,
                            total: data.data.total
                        },
                        dataSource: data.data.list
                    }
                )
            }
        });
    }

    goToAdd() {
        hashHistory.push("/platform/report/add")
    }

    goToDetail(id) {
        hashHistory.push("/platform/report/detail/" + id)
    }

    goToEdit(id) {
        hashHistory.push("/platform/report/edit/" + id)
    }

    deleteInfo(id) {
        let that = this;
        ajax({
            url: "/platform/report/deleteInfo",
            method: "post",
            data: {id: id},
            callback: function(data) {
                if(data.status == 200) {
                    message.success("删除成功", 1);
                    let newDataSource = that.state.dataSource.filter(item => { return item.key != id});
                    that.setState({dataSource: newDataSource});
                } else {
                    message.error(data.errMsg, 1);
                }
            }
        });
    }

    changePage(pagination) {
        this.getInfoList(pagination.current)
    }

    render() {
        return (
            <Layout style={{ padding: '0 24px 24px' }}>
                <Breadcrumb style={{ margin: '12px 0' }}>
                    {
                        this.props.routes.map(function(item, index) {
                            return <Breadcrumb.Item key="">{item.breadcrumbName}</Breadcrumb.Item>
                        })
                    }
                </Breadcrumb>
                <Content style={{ background: '#fff', padding: 24, margin: 0, minHeight: 280 }}>
                    <Button icon="plus" onClick={this.goToAdd} style={{"marginBottom": "20px"}}>添加媒体报道</Button>
                    <Table
                        bordered
                        dataSource={this.state.dataSource}
                        columns={this.state.columns}
                        pagination={this.state.pagination}
                        onChange={this.changePage}
                        loading={this.state.loading}
                    />
                </Content>
            </Layout>
        )
    }
}

module.exports = List;


